      //     // console.log(res);
      //     const { code, homeData  } = res.data;
      //     if (code == 200){
      //         data.big_sort = data.big_sort;
      //         data.small_sort = data.small_sort;
      //         data.centent_nav_list = data.centent_nav_list
      //     }
      // });
      // mockjs
    };

    onMounted(() => {
      getHomeData();
    });
    return {
      ...toRefs(data),
      active
    };
  }
};